/* styles.css */
@import url('https://fonts.googleapis.com/css2?family=Overpass:wght@200&display=swap');

.moveDateBody {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    font-family: Arial, sans-serif;
    background-color: wheat;
    margin: 0;
    font-family: 'Overpass', 'Serif';
  }
  
  .container {
    max-width: 800px;
    width: 100%;
    text-align: center;
    padding: 20px;
    background-color: wheat;
  }
  
  .header {
    margin-bottom: 20px;
  }
  
  .question {
    margin-bottom: 20px;
  }
  
  .movingDate,
  .settlementDate {
    margin-top: 10px;
    padding: 10px;
    width: 100%;
  }
  
  .settlementTime {
    margin-top: 10px;
    text-align: center;
  }
  
  .buttonContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px; 
    margin-top: 20px;
  }
  
  .backBtn,
  .nextQ {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  a {
    text-decoration: none;
    color: white;
  }
  